        Hito流行音樂獎頒獎典禮\n        ")]),_c('td',[_vm._v("\n          Hito流行音樂獎最初是由臺灣Hit\n          FM聯播網和亞洲6家重量級電臺：上海東方電臺、北京音樂臺、廣東音樂臺、香港電臺、新加坡Yes933電臺、馬來西亞988電臺共同主辦。各獎項是以「唱片行銷售量」、「數位音樂下載」以及「歌曲播出率」三大要素作為評分標準，另外最受歡迎系列獎項是由網民公投選出獎項得主。\n        ")]),_c('td',[_vm._v("\n          HitFm聯播網主辦\n        ")])]),_c('tr',[_c('td',[_vm._v("\n          2019/06/07\n        ")]),_c('td',[_vm._v("\n          甘噪祭\n        ")]),_c('td',[_vm._v("\n          甘噪祭發起於臺南，名稱源自府城的嗜甜習慣，以及獨立音樂的噪動本質。志在打開大眾聆聽習慣，打造不只受樂迷熱愛，一般人也能參與的新型態音樂節。夾腳拖、吊嘎，輕鬆隨節奏晃動；表演中間騎著摩托車隨意逛逛，左手提著菜市仔買來的香腸熟肉、右手拿著微糖微冰的青茶、嘴裡嚼著剛吃一口的旗魚黑輪。\n        ")]),_c('td',[_vm._v("\n          甘噪祭策展團隊、海安觀光商圈發展協會主辦\n        ")])]),_c('tr',[_c('td',[_vm._v("\n          2019/06/29\n        ")]),_c('td',[_vm._v("\n          金曲獎\n        ")]),_c('td',[_vm._v("\n          金曲獎是臺灣規模最大的音樂獎項，也是臺灣和華語地區具有聲譽及影響力的大型音樂獎勵活動，與金馬獎、金鐘獎並列為臺灣三大娛樂獎，合稱為「三金」。於1990年首次舉辦，每年頒發一次，為華人世界舉辦最久的流行音樂盛事。\n        ")]),_c('td')]),_c('tr',[_c('td',[_vm._v("\n          2019/07/23\n        ")]),_c('td',[_vm._v("\n          金門夏日音樂季\n        ")]),_c('td',[_vm._v("\n          金門夏日音樂季為金門暑假期間超強檔期的音樂盛宴，不僅4天全場免門票，還安排的超強卡司輪番演出。\n        ")]),_c('td')]),_c('tr',[_c('td',[_vm._v("\n          2019/07/28\n        ")]),_c('td',[_vm._v("\n          鐵玫瑰音樂節\n        ")]),_c('td',[_vm._v("\n          桃園展演中心自2012年起開始舉辦「桃園鐵玫瑰熱音賞」樂團大賽，2014年加入「高校熱音賞」；2015年首創鐵玫瑰「音樂學院」、舉辦「音樂展」、並新增媲美跨年演唱會人潮的鐵玫瑰「音樂節」活動；2016年再新增「搖滾繞境」演唱會，將搖滾樂帶到桃園各地區；2017年鐵玫瑰音樂節再次擴大辦理，於北桃園（展演中心）、南桃園（高鐵站前廣場）分別舉辦1場演唱會，並以音樂曲風作為區分，讓民眾能藉由活動認識臺灣多元豐富的流行音樂創作能量。\n        ")]),_c('td',[_vm._v("\n          桃園展演中心主辦\n        ")])]),_c('tr',[_c('td',[_vm._v("\n          2019/08/02-08/04\n        ")]),_c('td',[_vm._v("\n          2019原住民族國際音樂節\n        ")]),_c('td',[_vm._v("\n          PASIWALI是臺灣原住民族阿美族語，往東方的意思，東方是太陽升起的地方，臺灣的東方也是孕育許多原住民族群的地方。\n          Taiwan PASIWALI Festival是一場長達數十萬公里的南島之舟部落音樂旅程。\n        ")]),_c('td')]),_c('tr',[_c('td',[_vm._v("\n          2019/08/24\n        ")]),_c('td',[_vm._v("\n          臺灣原創流行音樂大獎\n        ")]),_c('td',[_vm._v("\n          臺灣原創流行音樂大獎由文化部影視及流行音樂產業局、原住民族委員會、客家委員會共同主辦，自2004年舉辦至今。臺灣原創流行音樂大獎鼓勵音樂創作者以臺灣最獨特的「河洛語、客語、原住民族語」進行音樂詞曲創作，傳承臺灣傳統的語言文化，並讓該獎項成為臺灣最具指標性的母語音樂創作平臺。\n        ")]),_c('td',[_vm._v("\n          文化部影視及流行音樂產業局、原住民族委員會、客家委員會主辦\n        ")])]),_c('tr',[_c('td',[_vm._v("\n          2019/10/11-10/20\n        ")]),_c('td',[_vm._v("\n          臺中爵士音樂節\n        ")]),_c('td',[_vm._v("\n          臺中爵士音樂節已邁向第十六年，隨著秋風將爵士搖擺傳承的因子散播在城市的每一個角落，讓每位熱愛爵士的靈魂萌芽。臺中爵士音樂節延續不變的承諾帶來高質量的演出，來自世界各地的爵士演奏者齊聚盡情搖擺。\n        ")]),_c('td')]),_c('tr',[_c('td',[_vm._v("\n          2019/11/23-11/24\n        ")]),_c('td',[_vm._v("\n          阿米斯音樂節\n        ")]),_c('td',[_vm._v("\n          2013年於臺東都蘭部落，創辨了第一屆阿米斯音樂節。遵循著部落老人家的囑咐「土地，跟人一樣，是需要休息的」連續辦兩年、休耕一年，即將在2019年11月，邁入第五屆阿米斯音樂節。這裡沒有節目表、也不主打強牌卡司。有的，是這塊土地最真切的溫度、以及在地族人真摯樸實的一面。\n        ")]),_c('td')]),_c('tr',[_c('td',[_vm._v("\n          2019/11/30-12/01\n        ")]),_c('td',[_vm._v("\n          愛之日常音樂節\n        ")]),_c('td',[_vm._v("\n          「愛之日常音樂節」於2018年創立，由音樂人郭蘅祈（郭子）發起，呼應全球12月1日World AIDS\n          Day（世界愛滋日），成為臺灣音樂圈第一個自發對愛滋與感染者的關懷與支持的活動。本活動推動公益募款，將目標置於堅實人與人、人與世界之間的愛、相信與希望。\n        ")]),_c('td')]),_c('tr',[_c('td',[_vm._v("\n          2019/12/14\n        ")]),_c('td',[_vm._v("\n          Adju阿督音樂節\n        ")]),_c('td',[_vm._v("\n          首次主辦的ADJU（原住民多元性別）音樂會，希望透過音樂消弭社會對ADJU的偏見歧視、撫慰受傷的心靈與關係、建立性別友善環境、支持ADJU多元發聲。\n        ")]),_c('td')])])])])}]

